
import { defineComponent } from "vue";

// unified wrapper for internal (router) or external (other-domain) links
export default defineComponent({
  props: {
    // location to link to
    to: String,
  },
  computed: {
    isExternal() {
      const url = String(this.to || "");
      return (
        url.startsWith("http") ||
        url.startsWith("ftp") ||
        url.startsWith("mailto:")
      );
    },
  },
});
